// in src/users.js
import React from "react";
import {
  List,
  Datagrid,
  TextField,
  Filter,
  TextInput,
  ReferenceInput,
  SelectInput,
  SimpleList,
  Responsive,
  ReferenceField,
  Show,
  SimpleShowLayout,
  DateField,
  ShowButton,
  TabbedShowLayout,
  Tab,
  //RichTextField,
  NumberField,
  BooleanField,
  ReferenceManyField,
  EditButton
} from "react-admin";

const PostFilter = props => (
  <Filter {...props}>
    <TextInput label="Search" source="q" alwaysOn />
    <ReferenceInput
      label="Status"
      source="statusId"
      reference="stransaksi"
      allowEmpty
    >
      <SelectInput optionText="status" />
    </ReferenceInput>
  </Filter>
);

export const ChekTransaksi = props => (
  <List title="Check Transaction" {...props} filters={<PostFilter />}>
    <Responsive
      small={
        <SimpleList
          primaryText={record => record.jenissampah}
          secondaryText={record => record.jumlahsampah}
          tertiaryText={
            record => record.tanggal
            // new Date(record.published_at).toLocaleDateString()
          }
        />
      }
      medium={
        <Datagrid>
          <TextField source="id" />

          <ReferenceField
            label="Penjual"
            source="customerId"
            reference="customers"
          >
            <TextField label="Nama Penjual" source="nama" />
          </ReferenceField>

          <TextField label="Jenis Sampah" source="jenissampah" />
          <TextField label="Jumlah Sampah" source="jumlahsampah" />
          <TextField source="tanggal" />

          <ReferenceField
            label="Status Transaksi"
            source="statusId"
            reference="stransaksi"
          >
            <TextField source="status" />
          </ReferenceField>

          <ShowButton />
        </Datagrid>
      }
    />
  </List>
);

export const TranShow = props => (
  <Show {...props}>
    <SimpleShowLayout>
      <ReferenceField label="Penjual" source="customerId" reference="customers">
        <TextField source="nama" />
      </ReferenceField>

      <ReferenceField label="No.HP" source="customerId" reference="customers">
        <TextField source="hp" />
      </ReferenceField>

      <ReferenceField label="Alamat" source="customerId" reference="customers">
        <TextField source="alamat" />
      </ReferenceField>

      <TextField label="Jenis Sampah" source="jenissampah" />
      <TextField label="Harga Sampah" source="hargasampah" />
      <TextField label="Tanggal" source="tanggal" />

      <ReferenceField label="Status" source="statusId" reference="stransaksi">
        <TextField source="status" />
      </ReferenceField>

      <TabbedShowLayout>
        <Tab label="Detail Penjual">
          <ReferenceField
            label="Penjual"
            source="customerId"
            reference="customers"
          >
            <TextField source="nama" />
          </ReferenceField>

          <ReferenceField
            label="No.HP"
            source="customerId"
            reference="customers"
          >
            <TextField source="hp" />
          </ReferenceField>

          <ReferenceField
            label="Alamat"
            source="customerId"
            reference="customers"
          >
            <TextField source="alamat" />
          </ReferenceField>
        </Tab>

        <Tab label="Detail Barang">
          <TextField label="Jenis Sampah" source="jenissampah" />
          <TextField label="Jumlah Sampah" source="jumlahsampah" />
          <TextField source="tanggal" />
        </Tab>

        <Tab label="Miscellaneous" path="miscellaneous">
          <TextField
            label="Password (if protected post)"
            source="password"
            type="password"
          />
          <DateField label="Publication date" source="published_at" />
          <NumberField source="average_note" />
          <BooleanField
            label="Allow comments?"
            source="commentable"
            defaultValue
          />
          <TextField label="Nb views" source="views" />
        </Tab>
        <Tab label="comments" path="comments">
          <ReferenceManyField
            reference="comments"
            target="post_id"
            addLabel={false}
          >
            <Datagrid>
              <TextField source="body" />
              <DateField source="created_at" />
              <EditButton />
            </Datagrid>
          </ReferenceManyField>
        </Tab>
      </TabbedShowLayout>
    </SimpleShowLayout>
  </Show>
);
